import { Component, OnInit } from '@angular/core';
import { MemberService } from '../services/member.service';

declare var swal: any;

@Component({
  selector: 'app-member',
  templateUrl: './member.component.html',
  styleUrls: ['./member.component.css']
})
export class MemberComponent implements OnInit {

  firstname: string;
  lastname: string;
  phone: string;
  email: string;
  address: string;
  skill: string;

  mySkill: any = ['เขียนโปรแกรม', 'ร้องเพลง', 'เล่นกีฬาฟุตบอล', 'เล่นกายกรรม'];

  alertStyle = {
    'display': 'none'
  }

  textWarning = {
    'border-color': 'none'
  }

  items: any;
  btnUpdate = 0;
  btnSubmit = 0;
  constructor(private memberService: MemberService) { }

  async getMemberAll() {
    await this.memberService.getMemberAll()
      .subscribe(data => {
        console.log(data);
        this.items = data;
      });
  }

  onUpdate(item) {
    if (item.mem_fname && item.mem_lname && item.mem_phone && item.mem_email) {
      this.btnUpdate = 1;
      let record = {};
      record['ID'] = item.mem_id;
      record['Fname'] = item.mem_fname;
      record['Lname'] = item.mem_lname;
      record['Phone'] = item.mem_phone;
      record['Email'] = item.mem_email;
      record['Skill'] = item.mem_skill;
      record['Address'] = item.mem_address;
      this.memberService.updateMember(record);
      swal({
        text: 'อัปเดตข้อมูลเรียบร้อยแล้ว',
        type: 'success',
        confirmButtonColor: '#3085d6',
        confirmButtonText: 'ตกลง',
      }).then(() => {
        this.btnUpdate = 1;
      });
    } else {
      this.btnUpdate = 0;
      this.alertStyle = {
        'display': 'block'
      }
      this.textWarning = {
        'border-color': 'red'
      }
    }
  }

  onCancel() {
    this.getMemberAll();

    this.alertStyle = {
      'display': 'none'
    }
    this.textWarning = {
      'border-color': 'gainsboro'
    }
  }

  onDelete(item) {
    // console.log(id);
    swal({
      text: 'ยืนยันการลบข้อมูลหรือไม่ ?',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'ยืนยัน',
      cancelButtonText: 'ยกเลิก'
    }).then(result => {
      this.memberService.deleteMember(item);
      swal({
        text: 'ลบข้อมูลเรียบร้อยแล้ว',
        type: 'success',
        confirmButtonColor: '#3085d6',
        confirmButtonText: 'ตกลง',
      }).then(result => {
        this.getMemberAll();
      });
    });
  }

  onSubmit() {
    if (this.firstname
      && this.lastname
      && this.phone
      && this.email) {
      let record = {};
      record['Firstname'] = this.firstname;
      record['Lastname'] = this.lastname;
      record['Phone'] = this.phone;
      record['Email'] = this.email;
      record['Address'] = this.address;
      record['Skill'] = this.skill;
      //console.log(record);
      this.memberService.postMember(record);
      swal({
        text: 'บันทึกข้อมูลเรียบร้อยแล้ว',
        type: 'success',
        confirmButtonColor: '#3085d6',
        confirmButtonText: 'ตกลง',
      }).then(() => {
        this.getMemberAll();
        this.btnSubmit = 1;
      });
    } else {
      this.btnSubmit = 0;
      this.alertStyle = {
        'display': 'block'
      }
      this.textWarning = {
        'border-color': 'red'
      }
    }
  }

  onReset() {
    this.firstname = '';
    this.lastname = '';
    this.phone = '';
    this.email = '';
    this.address = '';

    this.alertStyle = {
      'display': 'none'
    }
    this.textWarning = {
      'border-color': 'gainsboro'
    }
  }

  ngOnInit() {
    this.getMemberAll();
  }

}
