import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-game',
  templateUrl: './game.component.html',
  styleUrls: ['./game.component.css']
})

export class GameComponent implements OnInit {
  constructor(
    private route: ActivatedRoute,
    private router: Router
  ) { }

  hand: any = ['Paper', 'Scissors', 'Rock'];
  hands: any = ['Paper', 'Scissors', 'Rock'];
  hand_cpu: any;
  hand_shuffle: any;
  result: any = 'Select your card.';
  username: string;
  player_score = 0;
  check: boolean;
  played: any = 1;
  hand_cpu_list: any;
  count: any;
  selected: any;

  card_visible = {
    'display': 'block'
  }
  over = {
    'display': 'none'
  }

  shuffle(a) {
    for (let i = a.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
  }

  CPU() {
    this.count = 'ready';
    this.hand_shuffle = this.shuffle(this.hand);
    this.hand_cpu_list = [this.hand_shuffle[1], this.hand_shuffle[0], this.hand_shuffle[2]];
    return this.hand_cpu = this.hand_shuffle[0];
  }

  Compare(P) {
    this.played = 0;
    const CPU = this.CPU();
    // console.log(P, CPU);
    if (P === CPU) {
      this.result = 'Draw.';
    } else {
      if ((P === this.hands[0] && CPU === this.hands[2]) ||
        (P === this.hands[1] && CPU === this.hands[0]) ||
        (P === this.hands[2] && CPU === this.hands[1])) {
        this.player_score += 1;
        this.result = 'You Win.';
      } else {
        this.result = 'You Lose.';
        this.check = true;
        if (this.check) {
          this.result = 'GAME OVER.';
          this.card_visible = {
            'display': 'none'
          }
          this.over = {
            'display': 'block'
          }
        } else { }
      }
    }
    this.onTimer();
  }

  onSave(username, score) {
    if (username) {
      console.log(username, score);
    }
  }

  reset() {
    this.result = 'Select your card.';
    this.player_score = 0;
    this.card_visible = {
      'display': 'block'
    }
    this.over = {
      'display': 'none'
    }
  }

  onTimer() {
    this.count = 1;
    let test = setInterval(() => {
      this.count += 1;
    }, 1000);
    setTimeout(() => {
      this.count = '';
      clearInterval(test);
      this.count = 'ready';
      this.played = 1;
      if (!this.check) {
        this.result = 'Select your card.';
      }
    }, 3000);
  }

  onSelected(selected) {
    this.selected = selected;
  }

  ngOnInit() {
    this.CPU();
  }

}
