import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BackendurlService } from './backendurl.service';

@Injectable({
  providedIn: 'root'
})
export class MemberService {

  httpOption = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(
    private http: HttpClient,
    private backendUrl: BackendurlService
  ) { }

  postMember(record) {
    // console.log(record);
    const url = this.backendUrl.url + 'post_member_api.php';
    this.http.post(url, JSON.stringify(record), this.httpOption).subscribe();
  }

  getMemberAll() {
    const url = this.backendUrl.url + 'get_member_api.php';
    return this.http.get(url);
  }

  deleteMember(item) {
    // console.log(id);
    const url = this.backendUrl.url + 'delete_member_api.php';
    this.http.post(url, JSON.stringify(item), this.httpOption).subscribe();
  }

  updateMember(record) {
    // console.log(record);
    const url = this.backendUrl.url + 'update_member_api.php';
    this.http.post(url, JSON.stringify(record), this.httpOption).subscribe();
  }

}
